export { onScroll, onToTopBtn, showLoading, onLoadMoreBtn };

const toBtnTop = document.querySelector('.btn-to-top');
const loading = document.querySelector('.loading');

window.addEventListener('scroll', onScroll);
toBtnTop.addEventListener('click', onToTopBtn);


function onScroll() {
  const scrolled = window.pageYOffset
  const coords = document.documentElement.clientHeight

  if (scrolled > coords) {
    toBtnTop.classList.add('btn-to-top--visible')
  }
  if (scrolled < coords) {
    toBtnTop.classList.remove('btn-to-top--visible')
  }
}


function onToTopBtn() {
  if (window.pageYOffset > 0) {
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }
}

window.addEventListener('scroll', () => {
    const { scrollTop, scrollHeight, clientHeight } = document.documentElement;
	

    if (clientHeight + scrollTop >= scrollHeight - 5) {
        showLoading();
    }
});

function showLoading() {
    loading.classList.add('show');
	setTimeout (onLoadMoreBtn, 3000)
}


function onLoadMoreBtn() {
  page += 1;
  simpleLightBox.destroy();

  fetchImages(query, page, perPage)
    .then(({ data }) => {


      renderGallery(data.hits);


      simpleLightBox = new SimpleLightbox('.gallery a').refresh();
      loading.classList.remove('show');
      const totalPages = Math.ceil(data.totalHits / perPage);
      if (page > totalPages) {
        loadMoreBtn.classList.add('is-hidden');

        alertEndOfSearch();
      }
    })
    .catch(Notiflix.Notify.failure(error));
}

onScroll();
onToTopBtn();